@media screen and (min-width:800px) {
    html, body{
        height: 100vh;
        width: 100vw;
        background-color: rgb(39, 38, 38);
    }
    .navbar-brand{
        font-size: x-large;
    }
    .navbar-brand :hover{
        color: rgb(247, 12, 216);
    }
    nav{
        position: sticky;
        margin-top: 0;
        height: 11vh;
        background-color: rgba(164, 148, 201, 0.6);
        box-shadow: 0 3px 4px 0 rgba(0,0,0,0.5);
    }
    .navbar-nav{
        justify-content: end;
        /* align-items: center; */
        margin-left: 30%;
        font-size: larger;
        font-weight: 600;
    }
    .nav-item{
        margin-left: 20%;
    }
    .nav-item :active{
        color: rgb(187, 9, 163);
    }
    .nav-item :hover{
        color: rgb(247, 12, 216);
        border-top: rgb(240, 7, 58);
        cursor: pointer;
    }
    .Home{
        height: 100vh;
        width: 100vw;
        margin-top: 5%;
        background-color: rgb(39, 38, 38);
        display:flex;
        font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    }
    .hero{
        margin: 5%;
        font-family: 'EB Garamond', serif;
        font-family: 'Roboto Slab', serif;
        text-align:start;
        justify-content: start;
        color: rgb(132, 58, 216);
        text-shadow: 6px 4px 5px rgba(0,0,0,0.5);
        font-weight: 800;
    }
    .hero-image img{
        margin-top: 8%;
        border-radius: 20px;
        /* border: rgb(39, 38, 38) 2px solid; */
        background-color: rgba(0, 0, 0, 0.2);
        box-shadow: 5px 3px 5px 4px rgba(0,0,0,0.5);
    }
    .service-page{
        height: 100vh;
        width: 100vw;
    }
    .service-page{
        background: url('./assets/pexels-pixabay-270360.jpg') no-repeat;
        background-size: cover;
        box-shadow: 6px 6px 6px 6px rgba(0, 0, 0, 0.4);
    }
    .serv-showcase{
        color: aqua;
        font-family: 'Roboto Slab', serif;
        font-size: larger;
        text-align: center;
        height: 100%;
        width: 100%;
        background-color: rgba(0, 0, 0, 0.8);
    }
    ul{
        list-style-type: none;
    }
    .service-cards{
        display: flex;
        justify-content: space-evenly;
        font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    }
    .service-cards li{
        margin-top: 5%;
        border-radius: 10px;
    }
    .stacks{
        color: aqua;
    }
    .Techstack{
        margin-top: 3%;
        font-family: 'Roboto Slab', serif;
        align-items: center;
        text-align: center;
    }
    .stack-icons li{
        justify-content: space-evenly;
        margin: 2%;
    }
    .stack-icons{
        display: flex;
        /* background-color: rgba(164, 148, 201, 0.6); */
    }
    .stack-icons img{
        border-radius: 30px;
        box-shadow: 5px 3px 5px 4px rgba(0,0,0,0.5);
    }
    .About{
        display: flex;
        justify-content: space-evenly;
        margin-top: 8%;
    }
    .about-text{
        color: rgb(226, 239, 250);
        
    }
    .myimg img{
        border-radius: 20px;
        box-shadow: 5px 3px 5px 4px rgba(0,0,0,0.5);
    }
    .about-text{
        height:45vh;
        width: 45vw;
        text-align: center;
        background-color: rgba(0, 0, 0, 0.4);
        border-radius: 20px;
    }
}
